.App {
  text-align: center;
}

.App-icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 15%;
}

.App-main {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.btn-style {
  color: lightyellow;
  border-radius: 5px;
  background: teal;
  transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  margin-top: 20px;
}

.nav-bar {
  align-items: center;
  /* align-content: stretch; */
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  padding: 10px 10px 10px 10px;
  width: 100%;
  height: 50px;
  background-color: bisque;
}

p {
  font-size: 18px;
}

.contactsList {
  background-color: teal;
  list-style: none;
}

.nav-bar ul {
  display: flex;
  justify-content: center;
  list-style: none;
}

.nav-bar ul li {
  margin: 1vmin;
}
